import { Component, OnInit } from '@angular/core';
import { MainSharedService } from 'src/app/util/shared.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SellerService } from 'src/app/core/services/seller.service';
import { AlertMessageComponent } from 'src/app/shared/alert-message/alert-message.component';
import { CheckLoginService } from 'src/app/core/services/checklogin.service';
import { GtmService } from 'src/app/core/services/gtm.service';
import { SeoService } from 'src/app/core/services/header/seo.service';

@Component({
  selector: 'app-pre-analysis',
  templateUrl: './pre-analysis.component.html',
  styleUrls: ['./pre-analysis.component.scss'],
  providers: [AlertMessageComponent]
})
export class PreAnalysisComponent implements OnInit {

  private token: string;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private shared: MainSharedService,
    private sellerS: SellerService,
    private gtm: GtmService,
    private alertMsg: AlertMessageComponent,
    private checkLoginService: CheckLoginService,
    private seoService: SeoService
  ) { }

  ngOnInit() {
    this.checkLoginService.check();
    this.seoService.createLinkForCanonicalURL();
    this.gtm.addPageGTM('Pageview ZeroKM', this.router.url, 'Zero Km - Pedido Vendedor');
    this.token = this.route.snapshot.params.token;
    this.createOrder();
  }

  createOrder() {
    this.shared.setCounterLoading(true);
    this.sellerS.createOrderBySellerLink(this.token).subscribe(res => {
      if (res !== undefined && res != null && res && res.success) {
        this.shared.setCounterLoading(false);
        this.router.navigate([`/assinatura/pagamento/${res.data.id}`], { replaceUrl: true });
      } else {
        this.shared.setCounterLoading(false);
        const msg = 'O veículo não está mais disponível.';
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Desculpe',
          mensagem: msg
        }, 'message', true);
        this.router.navigate(['/']);
      }
    }, ex => {
      this.shared.setCounterLoading(false);
      const msg = 'O veículo não está mais disponível.';
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Desculpe',
        mensagem: msg
      }, 'message', true);
      this.router.navigate(['/']);
    })
  }
}
